const login = async () => {
  const url = `${RIOSCONCIENCIA_API.public}/Authenticate`;
  //hardcoded user - dev purpose
  const auth = {
    email: "admin@admin.com",
    password: "admin"
  };

  const res = await axios.post(url, auth);

  return res.data;
};

